import { type NextRequest, NextResponse } from "next/server"
import { verifyUser, listAllUsers } from "@/lib/user-store"

export async function POST(request: NextRequest) {
  try {
    const { email, password } = await request.json()

    // Validate input
    if (!email || !password) {
      return NextResponse.json({ error: "Email and password are required" }, { status: 400 })
    }

    // Debug: List all users
    console.log(`[v0] Current users in database:`)
    const allUsers = listAllUsers()
    allUsers.forEach(user => {
      console.log(`  - ${user.email} (${user.role}) - verified: ${user.isVerified}`)
    })

    // Verify user credentials
    const user = verifyUser(email, password)
    
    if (!user) {
      return NextResponse.json({ error: "Invalid email or password" }, { status: 401 })
    }

    // For sellers, check if they are verified
    if (user.role === "seller" && !user.isVerified) {
      return NextResponse.json({ 
        error: "Your seller account is pending verification. Please wait for admin approval." 
      }, { status: 403 })
    }

    // Create session token (mock implementation)
    const token = Buffer.from(`${email}:${Date.now()}`).toString('base64')
    
    // Create response with cookies
    const response = NextResponse.json({
      success: true,
      user: {
        email: user.email,
        role: user.role,
        fullName: user.fullName,
        storeName: user.storeName
      },
      message: "Login successful"
    })

    // Set authentication cookies
    response.cookies.set("auth_token", token, {
      httpOnly: true,
      secure: process.env.NODE_ENV === "production",
      sameSite: "lax",
      maxAge: 60 * 60 * 24 * 7, // 7 days
      path: "/"
    })

    response.cookies.set("user_role", user.role, {
      httpOnly: true,
      secure: process.env.NODE_ENV === "production",
      sameSite: "lax",
      maxAge: 60 * 60 * 24 * 7,
      path: "/"
    })

    response.cookies.set("user_email", email, {
      httpOnly: true,
      secure: process.env.NODE_ENV === "production",
      sameSite: "lax",
      maxAge: 60 * 60 * 24 * 7,
      path: "/"
    })

    return response

  } catch (error) {
    console.log("[v0] Login error:", error)
    return NextResponse.json({ error: "Login failed" }, { status: 500 })
  }
}
